section.contact-page {
	h2 {
		color:#fff;
		margin:0;
	}
}
.contact-page-button {
	margin-top:32px;
}
.contact-numbers {
	@include remove-default-list-style;
	li {
		float:left;
		a {
			color:#fff;
			@include rem(padding-left, 10px);
			@include rem(margin-right, 30px);
			.phone-icon {
				float:left;
				width:16px;
				height:16px;
				@include rem(margin-top, 5px);
				@include svg-bg('phone-icon', $position: center center, $repeat: no-repeat);
			}
			.email-icon {
				float:left;
				width:23px;
				height:16px;
				@include rem(margin-top, 5px);
				@include svg-bg('envelope-icon', $position: center left, $repeat: no-repeat);
			}
		}
	}
}
.address {
	clear:right;
	@include rem(font-size, 14px);
	@include rem(margin-top, 45px);
	@include bp(desktop) {
		@include rem(margin-top, 35px);
	}
}