.logo-slider {
	border-top:2px solid #e5e5e5;
	border-bottom:2px solid #e5e5e5;
	@include rem(padding, 25px 0 0 0);
	@include rem(margin-bottom, 25px);
}
.client-logo {
	cursor:pointer;
	img {
		@include filter(grayscale(100%));
		@include transition(filter 0.5s ease-out);
	}
	&:hover img {
		@include filter(none);
		@include transition(filter 0.5s ease-in-out);
	}
}
.owl-dots {
	text-align:center;
	@include bp(desktop) {
		position:absolute;
		width:100%;
		left:0;
		bottom:230px;
	}
	.owl-dot {
		width:12px;
		height:12px;
		display:inline-block;
		@include rem(margin, 0 4px);
		background:$brand-four;
    	border-radius: 10px;
		&.active {
			background:$brand-three;
		}
	}
}
.owl-prev {
	position:absolute;
	top:50%;
	left:0;
	width:20px;
	height:34px;
	text-indent:-5000px;
	@include rem(margin-top, -30px);
	@include svg-bg('left-arrow-small', $repeat: no-repeat);
	display:block;
	z-index:20;
	cursor:pointer;
	/*display:none;
	visibility:hidden;*/
	@include bp(tablet) {
		display:block;
		visibility:visible;
	}
	&.disabled {
		display:none;
	}
}
.owl-next {
	@extend .owl-prev;
	@include svg-bg('right-arrow-small', $repeat: no-repeat);
	left:auto;
	right:0;
}


/* 
 *  Owl Carousel - Animate Plugin
 */
.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both; }
.owl-carousel .owl-animated-in {
  z-index: 0; }
.owl-carousel .owl-animated-out {
  z-index: 1; }
.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut; }

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1; }

  100% {
    opacity: 0; } }

@keyframes fadeOut {
  0% {
    opacity: 1; }

  100% {
    opacity: 0; } }

/* 
 * 	Owl Carousel - Auto Height Plugin
 */
.owl-height {
  -webkit-transition: height 500ms ease-in-out;
  -moz-transition: height 500ms ease-in-out;
  -ms-transition: height 500ms ease-in-out;
  -o-transition: height 500ms ease-in-out;
  transition: height 500ms ease-in-out; }

/* 
 *  Core Owl Carousel CSS File
 */
 .owl-carousel-container {
	position:relative;
	background:$brand-five;
	@include bp(desktop) {
		@include rem(margin-top, 109px);
	}
 }
.owl-carousel {
	display: none;
	width: 100%;
	-webkit-tap-highlight-color: transparent;
	/* position relative and z-index fix webkit rendering fonts issue */
	position: relative;
	z-index: 1;
}
.home .owl-carousel {
	background-color:$brand-five;
}
.owl-carousel .owl-stage {
	position: relative;
	-ms-touch-action: pan-Y; 
}
.owl-carousel .owl-stage:after {
	content: ".";
	display: block;
	clear: both;
	visibility: hidden;
	line-height: 0;
	height: 0; 
}
.owl-carousel .owl-stage-outer {
	position: relative;
	overflow: hidden;
	/* fix for flashing background */
	-webkit-transform: translate3d(0px, 0px, 0px); 
}
.owl-carousel .owl-item {
	position: relative;
	min-height: 1px;
	float: left;
	min-height:250px;
	-webkit-backface-visibility: hidden;
	-webkit-tap-highlight-color: transparent;
	-webkit-touch-callout: none;
}
.our-promise-no-slider.owl-carousel .owl-item {
	min-height:auto;
	@include rem(margin-bottom, 50px);
}
.owl-carousel-clients .owl-item {
	min-height:80px;
	vertical-align: middle;
	@include display(inline-flex); 
	@include box-pack(center);
	@include justify-content(center);
	@include align-items(center);
	@include box-orient(vertical);
	@include rem(margin, 0 0 30px 0);
	
}
.owl-carousel-claims-slider  {
	@include rem(margin, 0 0 50px 0);
}
.owl-carousel-claims-slider .owl-item {
	min-height:inherit;
}

  .owl-carousel .owl-item img {
    display: block;
    width:auto;
	
	max-height:714px;
	margin:0 auto;
    -webkit-transform-style: preserve-3d; 
	}
  .owl-carousel .owl-nav.disabled, .owl-carousel .owl-dots.disabled {
    display: none; }
  .owl-carousel .owl-nav .owl-prev, .owl-carousel .owl-nav .owl-next, .owl-carousel .owl-dot {
    cursor: pointer;
    cursor: hand;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .owl-carousel.owl-loaded {
    display: block; }
  .owl-carousel.owl-loading {
    opacity: 0;
    display: block; }
  .owl-carousel.owl-hidden {
    opacity: 0; }
  .owl-carousel.owl-refresh .owl-item {
    display: none; }
  .owl-carousel.owl-drag .owl-item {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .owl-carousel.owl-grab {
    cursor: move;
    cursor: -webkit-grab;
    cursor: -o-grab;
    cursor: -ms-grab;
    cursor: grab; }
  .owl-carousel.owl-rtl {
    direction: rtl; }
  .owl-carousel.owl-rtl .owl-item {
    float: right; }

/* No Js */
.no-js .owl-carousel {
  display: block; }

/* 
 * 	Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease; }
.owl-carousel .owl-item img {
  transform-style: preserve-3d; }

/* 
 * 	Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000; }
.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  background: url("owl.video.play.png") no-repeat;
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  -webkit-transition: scale 100ms ease;
  -moz-transition: scale 100ms ease;
  -ms-transition: scale 100ms ease;
  -o-transition: scale 100ms ease;
  transition: scale 100ms ease; }
.owl-carousel .owl-video-play-icon:hover {
  -webkit-transition: scale(1.3, 1.3);
  -moz-transition: scale(1.3, 1.3);
  -ms-transition: scale(1.3, 1.3);
  -o-transition: scale(1.3, 1.3);
  transition: scale(1.3, 1.3); }
.owl-carousel .owl-video-playing .owl-video-tn, .owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none; }
.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease; }
.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
  height: 100%;
  width: 100%; }
