.wp-paginate {
	.title {
		display:none;
		visibility:hidden;
	}
	li a {
		font-size:(14px);
		font-weight:700;
		background:none;
		border:none;
		&:hover {
			color:#fff;
			background:$brand-three;
		}
	}
	li span.page.current {
		font-size:(14px);
		font-weight:700;
		color:#fff;
		background:$brand-three;
		border:none;
	}
}
.background.background--blue .wp-paginate {
	li a {
		color:#fff;
	}
	.gap {
		color:#fff;
	}
}