.social {
	position:relative;
	@include remove-default-list-style;
	@include bp(tablet) {
		float:right;
	}
	&__item {
		display:inline-block;
		@include rem(margin-left, 8px);
	}
	&__link {
		float:left;
	}
}
.social__item:first-child {
	margin-left:0;
}

footer .social {
	float:left;
	@include rem(margin, 10px 0 0 0);
	&__item {
		margin-left:0;
		@include rem(margin-right, 20px);
		svg path,
		svg ellipse,
		svg rect {
			fill:$brand-four;
		}
		&:hover {
			svg path,
			svg ellipse,
			svg rect {
				fill:$brand-one;
			}
		}
	}
	&__item:fist-child {
		margin-left:0;
	}
	svg {
		height:12px;
	}
}

.newsletter-row .social {
	float:right;
	&__item {
		margin-right:0;
		@include rem(margin-left, 8px);
	}
}