@media all {
	.featherlight {
		display: none;

		/* dimensions: spanning the background from edge to edge */
		position:fixed;
		top: 0; right: 0; bottom: 0; left: 0;
		z-index: 2147483647; /* z-index needs to be >= elements on the site. */

		/* position: centering content */
		text-align: center;

		/* insures that the ::before pseudo element doesn't force wrap with fixed width content; */
		white-space: nowrap;

		/* styling */
		cursor: pointer;
		background: #333;
		/* IE8 "hack" for nested featherlights */
		background: rgba(0, 0, 0, 0);
	}

	/* support for nested featherlights. Does not work in IE8 (use JS to fix) */
	.featherlight:last-of-type {
		background: rgba(0, 0, 0, 0.8);
	}

	.featherlight:before {
		/* position: trick to center content vertically */
		content: '';
		display: inline-block;
		height: 100%;
		vertical-align: middle;
		margin-right: -0.25em;
	}

	.featherlight .featherlight-content {
		/* make content container for positioned elements (close button) */
		position: relative;
		width:98%;
		/* position: centering vertical and horizontal */
		text-align: left;
		vertical-align: middle;
		display: inline-block;

		/* dimensions: cut off images */
		overflow: auto;
		padding: 25px 25px 0;
		border-bottom: 25px solid transparent;

		/* dimensions: handling small or empty content */
		min-width:  30%;

		/* dimensions: handling large content */
		margin-left: 5%;
		margin-right: 5%;
		max-height: 95%;

		/* styling */
		background: #fff;
		cursor: auto;

		/* reset white-space wrapping */
		white-space: normal;
		@include bp(desktop) {
			width:70%;
		}
	}

	/* contains the content */
	.featherlight .featherlight-inner {
		/* make sure its visible */
		display: block;
	}

	.featherlight .featherlight-close-icon {
		/* position: centering vertical and horizontal */
		position: absolute;
		z-index: 9999;
		top:15px;
		right:10px;

		/* dimensions: 25px x 25px */
		line-height: 25px;
		width: 25px;

		/* styling */
		cursor: pointer;
		text-align: center;
		font-family: Arial, sans-serif;
		background: #fff; /* Set the background in case it overlaps the content */
		background: rgba(255, 255, 255, 0.3);
		color: #000;
	}


	.featherlight .featherlight-image {
		/* styling */
		width: 100%;
	}


	.featherlight-iframe .featherlight-content {
		/* removed the border for image croping since iframe is edge to edge */
		border-bottom: 0;
		padding: 0;
	}

	.featherlight iframe {
		/* styling */
		border: none;
	}
}

/* handling phones and small screens */
@media only screen and (max-width: 1024px) {
	.featherlight .featherlight-content {
		/* dimensions: maximize lightbox with for small screens */
		margin-left: 10px;
		margin-right: 10px;
		max-height: 98%;

		padding: 10px 10px 0;
		border-bottom: 10px solid transparent;
	}
}


.page-box-popup-content {
	display: none;
	img {
		float:left;
		margin-right:4%;
		width:200px;
		margin-bottom:$gutter;
		@media only screen and (max-width: 767px) {
			width:100%;
		}
	}
	.popup-content-section {
		float:left;
		width:100%;
		h2 {
			color:$brand-two;
			word-break:keep-all;
			font-size:(16px);
			margin-top:(30px);
			margin-bottom:(4px);
		}
	}
	p {
	  font-size:(13px);
	  font-family: 'Open Sans', sans-serif;
	  &.job {
		  font-weight:700;
	  }
	  &.feature-text {
		  font-weight:700;
		  font-size:(14px);
	  }
  }
}
.page-box__form-container {
	background:$brand-one;
	@include rem(padding, 50px 30px 30px 30px);
}
.general-content-lightbox {
	color:#fff;
	@include rem(margin-bottom, 20px);
	@include rem(padding-bottom, 20px);
	border-bottom:1px solid #fff;
	background:#fff;
	position:relative;
	z-index:99;
	display:none;
}
.info-content-lightbox {
	color:#fff;
	@include rem(margin-bottom, 20px);
	position:relative;
	z-index:99;
	background:#fff;
	display:none;
}
.image-content-lightbox {
	@include rem(padding, 30px 0 10px 0);
	background:#fff;
	text-align:center;
	display:none;
}
.information-window {
	background:$brand-one;
	@include rem(padding, 80px 40px 80px 40px);
	display:none;
	p {
		color:#fff;
		@include rem(font-size, 24px);
		@include rem(line-height, 30px);
		font-family:"antartidard_esslight", sans-serif;
	}
}

/* Gallery buttons with lightbox */
.gallery {
	display:none;
}
.featherlight-next:hover, 
.featherlight-previous:hover {
	background:none;
}
.featherlight-next span,
.featherlight-previous span {
	display:block;
	width:50px;
	height:64px;
	@include rem(padding, 10px 10px);
}
.featherlight-next span {
	@include svg-bg('right-arrow-small', $position:center center, $repeat: no-repeat);
	background-size:20px 34px;
	background-color:rgba(0,0,0,0.5);
}
.featherlight-previous span {
	@include svg-bg('left-arrow-small', $position:center center, $repeat: no-repeat);
	background-size:20px 34px;
	background-color:rgba(0,0,0,0.5);
}
.featherlight-next:hover span,
.featherlight-previous:hover span {
	display: inline-block;
}