#aligncaptcharight {
	float:right;
	@include rem(margin-top, 10px);
}
.page-template-page-contact #aligncaptcharight {
	@include rem(margin-bottom, 30px);
}
form {
	@include inline-block;
}
label {
	cursor:pointer;
	display:block;
	@include rem(margin-top,5px);
	@include rem(margin-bottom, 8px);
}
input {
	@include inline-block;
	border:1px solid #fff;
	@include rem(padding, 6px 10px 4px 10px);
	border-radius:5px;
	color:#fff;
	@include rem(font-size, 15px);
	@include rem(margin-bottom, 10px);
	background:$brand-one;
	font-family: 'Lato', sans-serif;
	cursor:pointer;
	@include placeholder {
		color:#fff;
		opacity:1;
	}
	&:focus {
		outline: none;
	}
}
input[type=checkbox] {
	width:auto;
	height:auto;
	float:left;
	margin-top:6px;
	border-radius:none;
	border:none;
}
input[type=radio] {
	width:auto;
	height:auto;
	float:left;
}
.get-a-quote-form {
	input {
		color:$brand-one;
		background:none;
		border-color:$brand-one;
		cursor:auto;
	}
	input[type=submit] {
		color:#fff;
		background:$brand-two;
		border:none;
		@include rem(margin-top, 20px);
		cursor:pointer;
	}
}
select {
	@include appearance(none);
	width:100%;
	border:1px solid #fff;
	border-radius:5px;
	@include rem(padding, 6px 10px 4px 10px);
	@include rem(margin-bottom, 10px);
	@include rem(font-size, 15px);
	color:#fff;
	background:$brand-one;
	font-family: 'Lato', sans-serif;
	option {
		@include rem(padding, 2px 5px);
		color:$brand-one;
		background:#fff;
	}
}
textarea {
	@extend input;
	height:132px;
	@include rem(padding-top, 15px);
	@include rem(margin-bottom, 20px);
}
.page-template-page-get-a-quote textarea {
	margin-bottom:0;
	@include placeholder {
		color:$brand-one;
	}
}
.page-template-page-get-a-quote input {
	@include placeholder {
		color:$brand-one;
	}
}
input[type=submit] {
	clear:right;
	float:right;
	width:auto;
	@include rem(padding, 4px 10px 5px 10px);
	&:hover {
		background:darken( $brand-two, 10%);
		border-color:darken( $brand-two, 10%);
	}
}

/* Contact form 7 */
span.wpcf7-list-item {
	display:inline-block;
	margin-left:0;
	@include rem(margin-bottom, 4px);
}
span.wpcf7-list-item-label {
	position:relative;
	top:4px;
	color:#fff;
	@include rem(font-size, 14px);
	@include rem(margin-right, 10px);
}
/* IE10 fix */
html[data-useragent*='MSIE 10.0'] span.wpcf7-checkbox span.wpcf7-list-item-label {
	top:8px;
}
body .pop-up-download-form span.wpcf7-not-valid-tip {
	margin-top:10px;
}
.contact form.invalid span.wpcf7-not-valid-tip {
	color:#000;
	@include rem(margin, 10px 0 20px 0); 
}
.pop-up-download-form form.invalid input.wpcf7-not-valid {
	margin-top:0;
}
body .pop-up-download-form div.wpcf7-validation-errors {
	margin-top:0;
}
.mc4wp-form {
	margin-bottom:0;
	input {
		border-radius:0;
		background:none;
		border-colour:$brand-five;
		@include placeholder {
			opacity:0.8;
		}
	}
	input[type=submit] {
		float:left;
		background:$brand-one;
		border:none;
		@include rem(margin-top, 8px);
		margin-bottom:0;
	}
}
.mc4wp-alert {
	@include rem(font-size, 14px);
	@include rem(margin-top, 8px);
	border-radius:0;
	border:1px solid $error;
}

/* Quote form */
section fieldset {
	padding:0;
	border:none;
	@include rem(margin-bottom, 30px);
	.form-row {
		@include inline-block;
	}
	h3 {
		margin-top:0;
		@include rem(margin-bottom, 30px);
	}
}
.left-column {
	@include bp(desktop) {
		float:left;
		width:60%;
		padding-right:4%;
		border-right:1px solid #fff;
	}
}
.right-column {
	@include bp(desktop) {
		float:right;
		width:36%;
	}
}
fieldset label {
	@include bp(desktop) {
		float:left;
		width:35%;
	}
}
.form-col-fields {
	position:relative;
	@include bp(desktop) {
		float:right;
		width:60%;
	}
}
.form-col-fields-half {
	float:left;
	width:49%;
}
.custom-checkbox {
	position:relative;
	width:100%;
	line-height: 35px;
	@include rem(margin-bottom, 10px);
	span.wpcf7-list-item {
		position:relative;
		width:49%;
		margin-right:2%;
		@include rem(margin-bottom, 8px);
		&:nth-of-type(even) {
			margin-right:0;
		}
	}
	.wpcf7-list-item-label {
		position:absolute;
		top:0;
		left:0;
		width:100%;
		display:block;
		text-align:center;
		border-radius:5px;
		@include rem(font-size, 16px);
		background:$brand-one;
		cursor:pointer;
	}
	input[type="checkbox"]:checked + .wpcf7-list-item-label {
		background:$brand-three;
	}
	input[type="radio"]:checked + .wpcf7-list-item-label {
		background:$brand-three;
	}
}
.form-col-fields .date-fields {
	input {
		float:left;
		width:31.33333333333333%;
		margin-right:3%;
		&.last-child {
			margin-right:0;
		}
	}
}
.accept-checkbox {
	float:left;
	width:50%;
	label {
		float:left;
		width:auto;
		color:$brand-one;
		@include rem(font-size, 14px);
	}
	input {
		float:left;
		width:50%;
	}
	input[type="checkbox"] {
		float:left;
		width:25px;
		height:25px;
		padding:0;
		margin-left:2%;
		@include rem(margin-right, 10px);
	}
}

/* Form messages */
form.invalid input.wpcf7-not-valid {
	background:lighten($error, 40%);
	border:1px solid $error;
	margin-bottom:0;
}
div.wpcf7-spam-blocked { 
	color:$error;
	@include inline-block;
	border-radius:8px;
	@include rem(padding, 8px 20px 5px 20px);
	border:1px solid $error;
	background:lighten($error, 40%);
	@include rem(margin, 5px 0 30px 0);
}
span.wpcf7-not-valid-tip {
	color:$error;
	@include inline-block;
	@include rem(font-size, 13px);
	@include inline-block;
	@include rem(margin, 10px 0 20px 0); 
}
.right-column {
	span.wpcf7-not-valid-tip {
		text-align:right;
	}
}

.contact form.invalid span.wpcf7-not-valid-tip {
	color:$error;
	@include inline-block;
	@include rem(font-size, 13px);
	@include rem(margin, 10px 0 20px 0); 
}
body div.wpcf7-validation-errors {
	@include inline-block;
	@include rem(font-size, 13px);
	background: #ffe2e2;
	border:1px solid $error;
	border-radius:8px;
	@include rem(padding, 8px 20px);
	color:$error;
	@include rem(margin,20px 0 30px 0);
}
body .pop-up-download-form div.wpcf7-validation-errors {
	margin-top:0;
}
body .wpcf7-mail-sent-ok {
	@include inline-block;
	@include rem(font-size, 13px);
	background-color:$brand-eight;
	border:1px solid $brand-eight;
	@include rem(padding, 8px 20px);
	color:#fff;
	border-radius:8px;
	@include rem(margin, 20px 0 0 0);
}
