.question-answer-box {
	color:#fff;
	@include rem(padding, 10px 30px);
	background:$brand-three;
	@include rem(margin-bottom, 26px);
	h3 {
		color:#fff;
		@include rem(font-size, 24px);
		@include rem(margin-bottom, 26px);
	}
	img {
		@include rem(margin-bottom, 26px);
	}
	ul {
		@include rem(margin-left, 13px);
		li {
			color:$brand-two;
			list-style-type:disc;
			&:before {
				display:none;
			}
		}
	}
}
.answer-reveal {
	&-trigger {
		color:$brand-two;
		@include rem(font-size, 16px);
		font-family: 'Lato', sans-serif;
		font-weight:400;
		cursor:pointer;
		span {
			color:#fff;
		}
	}
	&-answer {
		display:none;
	}
}