.form-column-container {
	&.column-left {
		@include rem(padding-right, 40px);
		@include bp(tablet) {
			@include rem(padding-right, 50px);
			border-right:2px solid #fff;
		}
	}
}
.add-relative {
	position:relative;
}
.info-icon {
	display:inline-block;
	width:20px;
	height:20px;
	text-align:center;
	color:$brand-one;
	border:1px solid $brand-one;
	border-radius:50%;
	@include rem(margin-bottom,20px);
	@include bp(desktop) {
		position:absolute;
		top:50%;
		right:-35px;
		margin-top:-20px;
	}
}
.quote-process {
	position:relative;
	
	float:left;
	width:100%;
	@include rem(padding, 50px 10px 50px 10px);
	background:$brand-one;
	h3 {
		color:#fff;
		@include rem(font-size, 38px);
		@include rem(margin-bottom,30px);
	}
	.quote-note {
		p {
			color:#fff;
			text-align:center;
			@include rem(margin-top, 20px);
			margin-bottom:0;
		}
	}
}
.page-template-page-get-a-quote .quote-process {

}
.process-step {
	position:relative;
	@include rem(padding-left, 25px);
	@include rem(margin-bottom,20px);
	@include bp(tablet) {
		margin-bottom:0;
	}
	&:before {
		position:absolute;
		top:0;
		left:0;
		font-weight:700;
	}
	p {
		color:#fff;
		margin-bottom:0;
		font-weight:300;
		@include rem(font-size, 18px);
		@include rem(line-height, 24px);
		font-family:"antartidard_esslight", sans-serif;
	}
}
.process-step.step1 {
	&:before {
		content:"1.";
	}
}
.process-step.step2 {
	&:before {
		content:"2.";
	}
}
.process-step.step3 {
	&:before {
		content:"3.";
	}
}

.page-template-page-get-a-quote div.quote-note,
div.quote-note {
	@include rem(padding, 50px 10px 20px 10px);
	text-align:center;
	p {
		color:$brand-three;
		@include rem(font-size, 14px);
		@include rem(margin-bottom, 20px);
		margin-bottom:0;
	}
}